<template>
  <div class="risk-level-set">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">{{typeNm}}洗钱风险模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">风险等级设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <div class="button-content">
            <Button type="primary" @click="operate('1')" v-authcontrol="{menuCode:typeNm == '业务'?'111001001001':'111002001001'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增
            </Button>
            <Button type="primary" @click="operate('5')" v-authcontrol="{menuCode:typeNm == '业务'?'111001001004':'111002001004'}">
              <Icon type="ios-alarm-outline" />历史
            </Button>
            <Button type="primary" @click="exportFn" v-authcontrol="{menuCode:typeNm == '业务'?'111001001005':'111002001005'}">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>
                      <span class="spanBox" @click="operate('2', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001001002':'111002001002'}">修改</span>
                      <span class="spanBox" @click="operate('3', item)" v-authcontrol="{menuCode:typeNm == '业务'?'111001001003':'111002001003'}">删除</span>
                    </td>
                    <td>{{item.levelCode}}</td>
                    <td>{{item.levelName}}</td>
                    <td>{{item.minValue}}</td>
                    <td>{{item.maxValue}}</td>
                    <td>{{item.evalCycle}}</td>
                    <td>
                      <span class="spanBox" @click="operate('4', item)">详情</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
        <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%" v-if="showAddModal"></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addRiskLevel from "../common/riskLevelSet/addRiskLevel";
import history from "../common/riskLevelSet/history";
import riskLevelDetail from "../common/riskLevelSet/riskLevelDetail";
export default {
  components: {
    addRiskLevel,
    riskLevelDetail,
    history
  },
  data() {
    return {
      tabview:'addRiskLevel',
      showAddModal: false,
      dataObj:{
        data:{},
      },
      selectUrl: "",
      deleteUrl: "",
      modelTitle: "新增",
      typeNm: "业务",
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "等级代码" },
        { name: "等级名称" },
        { name: "下限分值" },
        { name: "上限分值" },
        { name: "评定周期（月）" },
        { name: "控制措施" }
      ],
      tableBody: [
      ]
    };
  },
  mounted() {},
  methods:{
    cancel() {},
    operate(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '新增'
          this.tabview = 'addRiskLevel';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:this.typeNm,
          };
          break;
        case '2':
          this.modelTitle = '修改'
          this.dataObj = {
            typeNm:this.typeNm,
            data:item
          };
          this.tabview = 'addRiskLevel';
          this.showAddModal = true;
          break;
        case '3':
          this.$Modal.confirm({ 
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
                this.axios
                .get(this.deleteUrl+"/"+item.id)
                .then(response => {
                  if (response.code == "200") {
                      this.$Message.success("操作成功");
                      this.axios
                      .get(this.selectUrl)
                      .then(response => {
                        if (response.code == "200") {
                            this.tableBody = response.data;           
                        }
                      })
                      .catch(error => {});
                  }
                })
                .catch(error => {});
            },
            onCancel: () => {
            }
          });
          break;
        case '4':
          this.modelTitle = '控制措施详情'
          this.tabview = 'riskLevelDetail';
          this.dataObj = {
            data:item
          };
          this.showAddModal = true;
          break;
        case '5':
          this.modelTitle = '历史详情';
          this.dataObj = {
            typeNm:this.typeNm
          };
          this.tabview = 'history';
          this.showAddModal = true;
          break;
      }
    },
    exportFn(){
      let url = this.typeNm == "业务"?"/model/business/doExportBusRiskLevel":"/model/product/doExportProRiskLevel"
      this.axios
        .get(url,{responseType: "blob"})
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    saveDatas(){
      this.showAddModal = false;
      this.axios
          .get(this.selectUrl)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;           
            }
          })
          .catch(error => {});
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
  },
  watch: {
    '$route':{
      handler(to, form){
        if (this.$route.params.type == 1) {
            this.typeNm = "业务";
            this.selectUrl = "/model/business/getBusRiskLevelView";
            this.deleteUrl = "/model/business/deleteBusRiskLevel";
        }else {
            this.typeNm = "产品";
            this.selectUrl = "/model/product/selectProRiskLevel";
            this.deleteUrl = "/model/product/delProRiskLevel";
        }
        this.axios
          .get(this.selectUrl)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;           
            }
          })
          .catch(error => {});
      },
      immediate:true
    },
  }
};
</script>
<style lang="scss" scoped>
.risk-level-set {
  .button-content {
    float: right;
  }
}
</style>


