<template>
    <div class="history">
        <Row style="padding-bottom:20px">
            <Button type="primary" @click="exportFn" style="float:right">
              <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
            </Button>
        </Row>
        
        <table class="tableBox" cellspacing="0" cellpadding="0">
            <thead>
                <tr>
                    <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in tableBody">
                    <td>{{pageSize*(pageNum-1)+index+1}}</td>
                    <td>{{item.dpname}}</td>
                    <td>{{item.updateTime}}</td>
                    <td>{{dataObj.typeNm == '业务'?item.username:item.updater}}</td>
                    <td class="show-detail" @click="shhowHistory(item.id)">详情</td>
                </tr>
            </tbody>
        </table>
         <div class="flexCenter pageBox">
            <Page :current.sync="pageNum" :total="rowCount"  @on-change="pageChange" :page-size="pageSize" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
        </div>
        <Modal :mask-closable="false"  
            v-model="showHistoryModal"
            :title="modelTitle"
            class="dialog"
            :styles="{width: '40%'}"
            >
            <component :is="tabview" v-if="showHistoryModal" width="40%" :id="id" :typeNm="dataObj.typeNm"></component>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>
<script>
import historyDetail from './historyDetail'
export default {
    props: ["dataObj"],
    created(){
        this.selectUrl = this.dataObj.typeNm == "业务"?"/model/business/getBusRiskLevelHis":"/model/proRiskLevelHis/getProRiskLevelHis"
        let data = {
            pageSize:this.pageSize,
            pageNum: this.pageNum,
        };
        data = JSON.stringify(data);
        this.axios
        .post(this.selectUrl, data, {
            headers: { "Content-Type": "application/json" }
        })
        .then(response => {
            if (response.code == "200") {
                this.rowCount = response.data.total;
                this.tableBody = response.data.records;           
            }
        })
        .catch(error => {});
    },
    data(){
        return{
            showHistoryModal:false,
            id: "",
            pageNum: 1,
            selectUrl: "",
            rowCount: 0,
            pageSize:10,
            modelTitle:'详情',
            tabview:'historyDetail',
            tableHeader1: [
                { name: "序" },
                { name: "部门" },
                { name: "更新时间" },
                { name: "更新人员" },
                { name: "更新内容" },
            ],
            tableBody: [],
        }
    },
    components:{
        historyDetail
    },
    methods:{
        shhowHistory(id){
            this.showHistoryModal = true;
            this.id = id;
        },
        exportFn(){
            let data = {
                pageSize:this.pageSize,
                pageNum: this.pageNum,
            };
            let url = this.dataObj.typeNm == "业务"?"/model/business/doExportBusRiskLevelHis":"/model/proRiskLevelHis/doExportProRiskLevelHis"
            this.axios
                .post(url,data, {headers: { "Content-Type": "application/json" },responseType: "blob"})
                .then(response => {
                    this.download(response,'导出文档.xls');
                })
                .catch(error => {});
        },
        download(data, name) {
        if (!data) {
            return;
        }
        let url = window.URL.createObjectURL(new Blob([data]));
        let link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", name);
        document.body.appendChild(link);
        link.click();
        },
        pageChange(pageNo) {
            this.pageNum = pageNo * 1;
            let data = {
                pageSize:this.pageSize,
                pageNum: this.pageNum,
            };
            data = JSON.stringify(data);
            this.axios
            .post(this.selectUrl, data, {
                headers: { "Content-Type": "application/json" }
            })
            .then(response => {
                if (response.code == "200") {
                    this.rowCount = response.data.total;
                    this.tableBody = response.data.records;           
                }
            })
            .catch(error => {});
        },
        pageSizefun(a){
            this.pageSize=a*1;
            if(this.pageNum == 1){
                this.pageChange(1);
            }
        }
    }
}
</script>
<style lang="scss">
    .history{
        .tab-title{
            font-size: 14px;
            color: #333;
            font-weight: bold;
        }
        table{
            width: 100%;
            font-size: 14px;
            color: #333333;
            margin-bottom: 20px;
            margin-top: 0;
            .show-detail{
                color: #0000CC;
                cursor: pointer;
                text-align: center;
            }
            &.first-tab{
                td{
                    border: .5px solid #ebecee;
                    height: 30px;
                    line-height: 30px;
                    padding-left: 20px;
                    &:nth-child(odd){
                        font-weight: bold;
                    }
                }
            }
            &.second-tab{
                thead{
                    tr{
                        td{
                            font-weight: bold;
                        }
                    }
                }
                tbody{
                    td{
                        &:nth-child(odd){
                            font-weight: normal;
                        } 
                    }
                }
            }
        }
    }
</style>


