<template>
  <div class="add-risk-level">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="150"
      class="form-content"
    >
      <FormItem label="等级代码" prop="levelCode">
        <Input v-model="formValidate.levelCode" placeholder="请输入等级代码" :disabled="!newItem"></Input>
      </FormItem>
      <FormItem label="等级名称" prop="levelName">
        <Input v-model="formValidate.levelName" placeholder="请输入等级名称"></Input>
      </FormItem>
      <FormItem label="下限分值" prop="minValue">
        <Input v-model="formValidate.minValue" placeholder="请输入下限分值"></Input>
      </FormItem>
      <FormItem label="上限分值" prop="maxValue">
        <Input v-model="formValidate.maxValue" placeholder="请输入上限分值"></Input>
      </FormItem>
      <FormItem label="评定周期（月）" prop="evalCycle">
        <Input v-model="formValidate.evalCycle" placeholder="请输入评定周期（月）"></Input>
      </FormItem>
      <FormItem label="控制措施" prop="controlMeasure">
        <Input
          v-model="formValidate.controlMeasure"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created() {
    if (this.dataObj.data) {
        this.formValidate = JSON.parse(JSON.stringify(this.dataObj.data));
        this.newItem = false;
    }
    this.typeNm = this.dataObj.typeNm;
  },
  data() {
    const validateMinValue = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      let maxValue = this.formValidate.maxValue;
      if (!value && value != 0){
          callback("下限分值不能为空");
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("下限分值请输入非负数"));
        return;
      }
      if (regu.exec(maxValue) && parseFloat(maxValue) < parseFloat(value)){
        callback("下限分值不能大于上限分值");
        return;
      } 
      if (parseFloat(value) > 999.99){
          callback("下限分值不能大于999.99");
      }else {
        callback();
      }
    };
    const validatorLevelCode = (rule, value, callback) =>{
      let url = this.typeNm == "业务"?`/model/business/isHaveBusRiskLevel/${value}`:`/model/product/isHaveProRiskLevel/${value}`
      if (this.newItem) {
        this.axios
          .get(url)
          .then(response => {
            if (response.data == "false") {
              callback(new Error("产品代码已存在"));
            } else {
              callback();
            }
          })
          .catch(error => {});
      } else {
        callback();
      }
    };
    const validateMaxValue = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      let minValue = this.formValidate.minValue;
      if (!value && value != 0){
          callback("上限分值不能为空");
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("上限分值请输入非负数"));
        return;
      }
      if (regu.exec(minValue) && parseFloat(minValue) > parseFloat(value)){
        callback("下限分值不能大于上限分值");
        return;
      } 
      if (parseFloat(value) > 999.99){
          callback("上限分值不能大于999.99");
      }else {
        callback();
      }
    };
    return {
      formValidate: {
        levelCode: "",
        remark: "",
        levelName: "",
        minValue: "",
        maxValue: "",
        evalCycle: "",
        controlMeasure: "",
      },
      newItem: true,
      typeNm: "",//业务/产品
      ruleValidate: {
        levelCode: [
          {
            required: true,
            message: "等级代码不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 32,
            message: "等级代码不能超过32个字！",
            trigger: "blur"
          },
          {
            validator: validatorLevelCode,
            trigger: "xxxx"
          }
        ],
        levelName: [
          {
            required: true,
            message: "等级名称不能为空！",
            trigger: "blur"
          },
          {
            type: "string",
            max: 128,
            message: "等级名称不能超过128个字！",
            trigger: "blur"
          }
        ],
        minValue: [
          {
            required: true,
            validator: validateMinValue,
            trigger: "blur"
          }
        ],
        maxValue: [
         {
            required: true,
            validator: validateMaxValue,
            trigger: "blur"
          }
        ],
        evalCycle: [
          {
            pattern: /^[1-9]+[0-9]*$/,
            message: "请输入正整数",
            trigger: "blur"
          },
          {
            validator: (rule, value, callback) => {
                        if (!value){
                            callback();
                            return;
                        }
                        if (parseInt(value) > 1000){
                            callback("最大不能超过1000");
                        }else {
                          callback();
                        }
            },
            trigger: "blur"
          }
        ],
        remark: [
          {
            type: "string",
            max: 512,
            message: "备注不能超过512个字！",
            trigger: "blur"
          }
        ],
        controlMeasure: [
          {
            type: "string",
            max: 512,
            message: "控制措施不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      let url = "";
      if (this.typeNm == "业务") {
        url = this.newItem == true?"/model/business/saveBusRiskLevel":"/model/business/updateProRiskLevel";
      }else{
        url = this.newItem == true?"/model/product/saveProRiskLevel":"/model/product/updateProRiskLevel";
      }
      this.$refs[name].validate(valid => {
        if (valid) {
            this.axios
            .post(url, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                this.$emit("save");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-risk-level {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


