<template>
    <div class="history-detail">
        <div class="content">
            <div class="left">
                <Form :label-width="120">
                    <FormItem label="操作时间">
                        <label>{{arr1.updateTime}}</label>
                    </FormItem>
                    <FormItem label="等级代码">
                        <label :class="fields.indexOf('levelCode') > -1?'red':''">{{arr1.levelCode}}</label>
                    </FormItem>
                    <FormItem label="等级名称">
                        <label :class="fields.indexOf('levelName') > -1?'red':''">{{arr1.levelName}}</label>
                    </FormItem>
                    <FormItem label="下限分值">
                        <label :class="fields.indexOf('minValue') > -1?'red':''">{{arr1.minValue}}</label>
                    </FormItem>
                    <FormItem label="上限分值">
                        <label :class="fields.indexOf('maxValue') > -1?'red':''">{{arr1.maxValue}}</label>
                    </FormItem>
                    <FormItem label="评定周期（月）">
                        <label :class="fields.indexOf('evalCycle') > -1?'red':''">{{arr1.evalCycle}}</label>
                    </FormItem>
                    <FormItem label="控制措施">
                        <label :class="fields.indexOf('controlMeasure') > -1?'red':''">{{arr1.controlMeasure}}</label>
                    </FormItem>
                </Form>
            </div>
            <div class="line"></div>
            <div class="right">
               <Form :label-width="120">
                    <FormItem label="操作时间">
                        <label>{{arr2.updateTime}}</label>
                    </FormItem>
                    <FormItem label="等级代码">
                        <label :class="fields.indexOf('levelCode') > -1?'red':''">{{arr2.levelCode}}</label>
                    </FormItem>
                    <FormItem label="等级名称">
                        <label :class="fields.indexOf('levelName') > -1?'red':''">{{arr2.levelName}}</label>
                    </FormItem>
                     <FormItem label="下限分值">
                        <label :class="fields.indexOf('minValue') > -1?'red':''">{{arr2.minValue}}</label>
                    </FormItem>
                    <FormItem label="上限分值">
                        <label :class="fields.indexOf('maxValue') > -1?'red':''">{{arr2.maxValue}}</label>
                    </FormItem>
                    <FormItem label="评定周期（月）">
                        <label :class="fields.indexOf('evalCycle') > -1?'red':''">{{arr2.evalCycle}}</label>
                    </FormItem>
                    <FormItem label="控制措施">
                        <label :class="fields.indexOf('controlMeasure') > -1?'red':''">{{arr2.controlMeasure}}</label>
                    </FormItem>
                </Form>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ["id","typeNm"],
    created(){
        let url = this.typeNm == "业务"?"/model/business/getBusRiskLevelHis":"/model/proRiskLevelHis/getProRiskLevelHis"
        this.axios
        .get(url+"/"+this.id)
        .then(response => {
            if (response.code == "200") {
                if (response.data.records.length > 1) {
                    this.arr1 = response.data.records[0];
                    this.arr2 = response.data.records[1];
                } else if (response.data.records.length > 0) {
                    this.arr1 = response.data.records[0];
                }
                this.fields = response.data.fields;
            }
        })
        .catch(error => {});
    },
    data(){
        return{
            arr1: [],
            arr2: [],
            fields:[],
        }
    }
}
</script>
<style lang="scss">
    .history-detail{
        .content{
            display: flex;
            justify-content:space-around;
            .line{
                width: 1px;
                background: #E8EAEC;
                margin-top: -16px;
            }
            .ivu-form-item-label {
                font-size: 14px !important;
                text-align: left;
            }
            .ivu-form-item-content{
                label{
                    font-size: 14px !important;
                }
            }
        }
        .red{
            color: red;
        }
    }

</style>


